.lang_card {
    text-align: center;
    cursor: pointer;
}

.lang_card:hover {
    transform: translateY(-0.25rem) !important;
}

.lang_logo {
    height: 3rem;
    margin: 0 auto;
    padding: 0.3rem;
}

.lang_text {
    position: absolute;
    left: 0;
    margin: auto;
    right: 0;
    opacity: 0;
    width: 80%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0.8rem;
    line-height: 1.5;
}

.lang_name {
    font-size: 0.9rem;
    opacity: 0.8;
}

