.tool_filter {
    color: rgba(255, 255, 255, 0.763);
    color: rgba(0, 0, 0, 0.700);
    font-size: 80%;
    padding: 1rem;
    text-align: center;
    margin: 0 0.25rem 2rem;
    transition: all 0.25s ease;
    color: rgb(187, 187, 187);
    padding: 0.5rem 1rem;
    border-radius: 10px;
    color: rgb(63, 63, 63);
}

@keyframes clicked {
    0% {
        opacity: 1
    }
    50% {
        opacity: 0.65;
    }
    100% {
        opacity: 1
    }
}

.tool_filter:active {
    animation: clicked 0.4s ease;
}

.tool_filter:hover {
    padding: 0.5rem 1rem;
    color: rgba(41, 41, 41, 0.903);
    color: rgb(154, 154, 154);
    transform: translateY(-5px);
    cursor: pointer;
}

.selected_filter {
    color: rgba(225, 225, 225, 0.903);
    color: rgba(0, 0, 0, 0.749);
    transform: translateY(-5px);
}

#skills {
    padding: 0 0.75rem;
}

.tool_card {
    margin: 0.5rem auto;
    text-align: center;
    transition: all 0.25s ease;
}

#skills {
    border: 1.2px solid black;
    height: 28vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media(max-height: 500px) {
    .slider-tools-cont{
        display: none;
    }
